body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Global Styles */
header {
  width: 100%;
  background-color: #eeeeee;
  /*background: #d0cdcd;*/
  font-size: 0.6rem;
  text-transform: uppercase;
  padding: 10px;
  font-weight: bold;
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  background: #d1cece;
  width: 100%;
  text-align: center;
  font-style: italic;
}

header footer {
  width: 100%;
  background: #f3f2f2;
  font-size: 1.2rem;
  font-style: italic;
  padding: 10px;
  font-weight: bold;
  text-align: center;
  height: 5vh;
}

.page-title {
  color: rgb(23, 29, 59);
  font-size: 0.9rem;
  font-weight: bold;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-style: italic;
  display: inline-block;
  margin-left: 46%;
  margin-right: 80px;
  margin-top: 30px;
  text-transform: uppercase;
}

.nav-item {
  color: black;
  margin: 10px;
  text-decoration: none;
}
.active-nav-item {
  text-decoration-line: underline;
  text-decoration-thickness: 2px;
  -moz-text-decoration-line: ;
  text-decoration-color: rgb(50, 143, 154);
  -moz-text-decoration-color: rgb(50, 143, 154);
}
.user-container {
  background: #fafafa;
  width: 90%;
  margin: 30px auto;
  font-size: 0.8rem;
  display: grid;
  grid-template: "content content content content update delete";
}
.user-details-container {
  width: 60%;
  margin: 10px 30px;
  display: inline-block;
  padding: 10px;
  grid-area: content;
}

button {
  text-transform: uppercase;
  color: white;
  border: none;
  min-width: 100px;
  border-radius: 0.3rem;
  padding: 5px 40px;
  font-size: 0.6rem;
}
button:hover {
  cursor: pointer;
}
.update-btn-container {
  grid-area: update;
}

.delete-btn-container {
  grid-area: delete;
}
.update-btn-container,
.delete-btn-container {
  display: inline-block;
  margin: 10px;
  padding: 10px;
}

.update-btn {
  background: rgb(59, 184, 198);
  margin-top: 70px;
}

.delete-btn {
  background: rgb(115, 22, 22);
  border: rgb(83, 23, 23);
  margin-top: 70px;
}

.add-btn,
.cancel-btn {
  background: rgb(59, 184, 198);
  border: rgb(50, 143, 154);

  display: inline-block;
}

.cancel-btn {
  background: rgb(115, 22, 22);
}

.user-control {
  margin: 5px;
}

.user-info {
  background-color: #dcdada;
  border-radius: 0.3rem;
  -webkit-border-radius: 0.3rem;
  -moz-border-radius: 0.3rem;
  -ms-border-radius: 0.3rem;
  -o-border-radius: 0.3rem;
  margin-right: 10px;
  text-transform: capitalize;
}

.form-btn {
  display: inline-block;
  margin-top: 20px;
  margin-left: 80px;
}

.form-item {
  font-size: 0.6rem;
  text-align: center;
}

.form-item label {
  display: inline-block;
  margin: 10px;
  padding: 10px;
  min-width: 80px;
}

.form-item input {
  margin: 10px 10px 10px 0;
  padding: 5px 5px 5px 0;
  min-width: 300px;
  background-color: rgb(238, 240, 240);
  font-size: 0.4rem;
}

.user-form {
  text-align: center;
  border: 1px solid rgb(183, 191, 196);
  padding-bottom: 10px;
  width: 50%;
  margin: 10px auto;
}

.add-user-status span {
  color: rgb(38, 163, 177);
  margin-left: 50px;
  text-transform: uppercase;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 0.3rem;
}
